@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500&family=Luxurious+Script&family=Roboto&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgba(65, 65, 65, 1);
  font-family: "Inter", sans-serif;
  color: rgba(250, 241, 232, 1);
  font-weight: 300;
  overflow-x: hidden;
}
.wrapper {
  margin: 50px auto;
  position: relative;
}

header {
  position: relative;
}

header .navbar {
  width: 100%;
  border-bottom: 1px rgba(158, 153, 149, 1) solid;
}
header ul.nav {
  float: right;
  list-style: none;
}

header ul.nav li {
  display: inline;
  margin-left: 25px;
  cursor: pointer;
  transition: opacity 500ms ease;
}

header ul.nav li:hover {
  opacity: 50%;
}

header .shop-cart-button {
  float: right;
  cursor: pointer;
  transition: opacity 500ms ease;
}

header .shop-cart-button:hover,
header .shop-cart-button.active {
  opacity: 50%;
}

header .shop-cart {
  position: absolute;
  top: 3em;
  width: 400px;
  right: 0;
  z-index: 1000;
  background-color: rgb(98, 97, 97);
  padding: 20px;
  padding-bottom: 0;
  color: rgba(250, 241, 232, 1);
}

header .shop-cart .item {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 1em;
}

header .shop-cart .item img {
  width: 80px;
  margin-right: 20px;
}
header .shop-cart .item h3 {
  font-size: 17px;
}

header .shop-cart .empty {
  font-size: 16px;
  margin-bottom: 20px;
}

header .shop-cart .item .delete-icon {
  color: rgba(250, 241, 232, 1);
  margin-left: 9em;
  cursor: pointer;
  transition: opacity 500ms ease;
}
header .shop-cart .item .delete-icon:hover {
  opacity: 50%;
}
header .shop-cart .item ul {
  padding: 10px 5px;
  list-style: none;

  align-items: center;
}

header .shop-cart .item ul li {
  transition: opacity 500ms ease;
}

.presentation {
  margin: 50px 0;
  width: 100%;
  background-size: cover;
  background-position: left center;
  background-blend-mode: multiply;
  position: relative;
  transition: background-size 0.5s; /* Добавляем плавный переход для background-size */
}
@media screen and (max-width: 768px) {
  .presentation {
    width: 100%;

    background-size: cover;
  }
}
header .presentation::after {
  content: "Bespoke";
  position: absolute;
  top: 4.75em;
  left: 3.605em;
  font-family: "Luxurious Script", cursive;
  font-size: 8.5em;
  font-weight: 400;
  line-height: 144px;
  color: rgba(217, 243, 255, 1);
}

header .presentation-right {
  margin-top: 10em;
}

header .presentation-right p {
  width: 22.5em;
  margin-bottom: 2.5em;
}

header .presentation-right .show-more {
  margin-top: 2.5em;
  margin-left: 10em;
}

.categories {
  display: flex;
  margin-bottom: 25px;
}

.categories select {
  background-color: transparent;

  border: rgba(250, 241, 232, 1) solid 1px;
  color: rgba(250, 241, 232, 1);
  padding: 10px 20px;

  margin-right: 25px;
  cursor: pointer;
}
.categories option {
  background-color: rgba(65, 65, 65, 1);

  border: rgba(250, 241, 232, 1) solid 1px;
  color: rgba(250, 241, 232, 1);
  padding: 10px 20px;
  margin: 10px;
  padding: 30px 10px;
  cursor: pointer;
}

.categories option::before {
  padding: 20px 20px;
  cursor: pointer;
}

.full-item {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 9999;
  overflow: auto;
}

.full-item div {
  position: fixed;
  width: 500px;
  position: relative;
  margin: 10% auto;
  padding: 40px 30px;
  background-color: rgb(98, 97, 97);
}
.gender-category h2 {
  margin-bottom: 0px;
  text-align: center;
  margin-right: 3em;
  font-family: "Luxurious Script", cursive;
  font-size: 55px;
  font-weight: 400;
  color: rgba(217, 243, 255, 1);
}

.gender-category h3 {
  margin-top: -0.7em;
  padding-top: 0px;
  margin-left: 3em;
  margin-bottom: 1.5em;
  content: "SPORTSWEAR";
  text-align: center;
  font-size: 24px;
  font-weight: 400;
  color: rgba(250, 241, 232, 1);
}
main {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}

main .item {
  color: rgba(250, 241, 232, 1);
  margin-bottom: 50px;
  padding-bottom: 20px;
  overflow: hidden;
  position: relative;
}

main img,
.full-item img {
  width: 100%;
  margin-bottom: 10px;
  transition: transform 500ms;
  cursor: pointer;
}

main img:hover {
  transform: scale(1.05) translateY(-10px);
}

main h2,
main p {
  margin: 10px 0;
  display: flex;
}
main .item ul {
  padding: 10px 5px;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.full-item ul {
  padding: 0.1em 5px;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.full-item h2 {
  font-size: 1.4em;
  margin-top: 0.5em;
}

.close-item {
  position: absolute;
  background-color: transparent;
  padding: 0.5em 1.1em;
  border-radius: 50%;
  color: rgba(250, 241, 232, 1);
  border: none;
  font-size: 1.5em;
  top: -0.4em;
  right: -0.8em;
}


.cart-sum{
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px rgba(158, 153, 149, 1) solid;
}

.cart-sum h2{
  font-weight: 400;
  font-size: 1.125em;
}

.cart-sum p{
  font-size: 1.125em;
  font-weight: 500;
  color: rgba(217, 243, 255, 1);

}
.sum {
  display: flex;
  text-align: right;
  font-size: 1.2em;
  padding-top: 0.5em;
  margin-right: 0.2em;
}

.sum span {
  opacity: 70%;
  font-size: 0.9em;
}

main ul li {
  transition: opacity 500ms ease;
}
main .composition {
  opacity: 0.5;
}
main ul span:not(.composition) {
  float: right;
  display: block;
  background-color: rgba(250, 241, 232, 1);
  height: 1px;
  width: 3em;
  margin-top: 6%;
  margin-left: 2em;
}

main .add-to-cart,
.full-item .add-to-cart,
.show-more {
  background-color: rgba(250, 241, 232, 1);
  text-align: center;
  color: rgba(65, 65, 65, 1);
  padding: 10px 20px;
  cursor: pointer;
  font-weight: 100;
  transition: 500ms;
}

main .add-to-cart:hover,
.full-item .add-to-cart:hover,
.show-more:hover {
  background-color: rgba(65, 65, 65, 1);
  color: rgba(250, 241, 232, 1);
  border: rgba(250, 241, 232, 1) 1px solid;
}

.show-more {
  display: block;
  width: 12em;
  border: none;
  margin: -2em auto 4em auto;
}

.about-us h3 {
  font-family: Inter;
  font-size: 24px;
  font-weight: 400;
  line-height: 29px;
  text-align: center;
  color: rgba(250, 241, 232, 1);
  margin-bottom: 2.5em;
  margin-top: 1em;
}

.about-us {
  margin-bottom: 8em;
}

.about-us .reasons-img img {
  width: 100%;
}

.about-us .reasons {
  margin-top: 3em;
}

.about-us .reason {
  display: flex;
  color: rgba(250, 241, 232, 1);
  margin-bottom: 2em;
}

.about-us .reason img {
  margin-right: 2em;
}

.about-us p {
  margin-top: 1em;
}

.about-us .reasons div:first-child span,
.about-us .reasons div:last-child span {
  border: solid;
  padding: 10px 15px;
}
.stuff {
  margin-top: 4em;
  margin-bottom: 2em;
  justify-content: space-between;
  margin-right: 5em;
}

.footer-nav ul {
  list-style: none;
}

footer {
  border-top: 1px rgba(158, 153, 149, 1) solid;
  padding-top: 2em;
}

.footer-nav ul {
  padding-left: 0;
  padding-top: 1em;
}

footer li {
  margin: 0.5em 0;
}

footer li:not(.footer-question) {
  color: rgba(250, 241, 232, 1);
  cursor: pointer;
  transition: opacity 300ms;
}

footer li:not(.footer-question):hover {
  opacity: 50%;
}

footer .footer-question {
  color: rgb(147, 145, 144);
  margin-bottom: -0.32em;
}

footer img {
  margin-top: 0.6em;
  margin-right: 1em;
  cursor: pointer;
  transition: opacity 300ms;
}

footer img:hover {
  opacity: 50%;
}

@media screen and (max-width: 1200px) {
  header .presentation::after {
    top: 4em;
    left: 2.1em;
    font-size: 7.5em;
  }
  header .presentation-left img {
    width: 84%;
  }

  header .presentation-right{
    margin-top: 8em;
  }

  header .presentation-right p{
    width: 18em;
  }

  header .presentation-right img{
    width: 95%;
  }

  header .presentation-right .show-more {
    margin-top: 2.5em;
    margin-left: 5em;
}
}



@media screen and (max-width: 991px) {
  .full-item ul {
    font-size: 1em;
  }

  .full-item h2 {
    font-size: 1.3em;
  }

  .presentation-left img {
    width: 100%;
    margin-left: -0.7em;
  }

  header .presentation-right {
    margin-top: 3em;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .presentation-right img {
    display: none;
  }

  header .presentation-right p {
    text-align: center;
  }

  header .presentation::after {
    top: 4em;
    left: 2.1em;
    font-size: 7.5em;
  }

  header .presentation-right .show-more {
    margin-top: 1em;
    margin-left: 50%;
    transform: translateX(-50%);
  }
}
